@use "sass:math";



// インナー幅設定
// コンテンツのインナー幅（最も共通の幅が多い部分）
$l-inner: 1266px;
// ※キュンパス幅ではなくインナーの幅を指す

// 余白
$padding-pc: 25px;
$padding-sp: 20px;
// ※PC時：デザインカンプの余白の幅ではない。PC時は通常25pxで大丈夫！

////////////////////
// フォント設定
////////////////////

$base-font-family: "Noto Sans JP", sans-serif;
$second-font-family: "Inter", sans-serif;
$third-font-family: "Josefin Sans", sans-serif;

// ※検証ツールでしっかり反映されているか確認する

// フォントウェイト
$light: 300;
$regular: 400;
$medium: 500;
$bold: 700;

////////////////////
// 色の指定
////////////////////

//色指定（共通性のあるものは出来る限り指定すると良い）
$base-color: #F2F2F2;
$font-color: #262626;
$accent-white: #FFFFFF;
$accent-yellow: #FBD700;
$accent-black: #4A4A4A;
$black: #000000;


body {
  font-family: $base-font-family;
  color: $font-color;
  background-color: #F5F5F5;
  font-weight: $regular;
  line-height: 1;
}


