.c-breadcrumb {
  width: 100%;
  margin: auto;
}
.c-breadcrumb__items {
  display: flex;
  align-items: center;
  flex-wrap: wrap; /* パンくずリストが長くなったときに折り返す */
}

.c-breadcrumb__item {
  display: inline-block;
  font-size: 1rem;
  letter-spacing: 0.03em;
  font-family: "Inter", sans-serif;
  font-weight: 700;
  display: flex;
  text-transform: capitalize;
  white-space: nowrap;
  position: relative;
  margin-right: 2.1875rem;
  transition: all 0.3s;
}
@media screen and (min-width: 900px) {
  .c-breadcrumb__item {
    font-size: 1.5rem;
  }
}

.c-breadcrumb__item:hover {
  opacity: 0.7;
}

.c-breadcrumb__item:hover:last-of-type {
  opacity: 1;
}

/* エリプシス処理｜ブログやニュース記事タイトルが表示されることを想定 */
.c-breadcrumb__item:last-of-type {
  white-space: normal;
  display: -webkit-box;
  -webkit-box-orient: vertical;
  -webkit-line-clamp: 2; /* 表示する行数を2行に設定 */
  overflow: hidden;
  color: #000000;
  margin-right: 0;
}

.c-breadcrumb__item:not(last-of-type) {
  color: #8A8A8A;
}

.c-breadcrumb__item::after {
  content: "";
  display: inline-block;
  color: #8A8A8A;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
  right: -1.3125rem;
  mask-image: url(../../assets/images/common/breadcrumb-arrow.svg);
  mask-repeat: no-repeat;
  mask-position: center;
  mask-size: contain;
  background-color: #262626;
  width: 0.4375rem;
  height: 0.9375rem;
}

.c-breadcrumb__item:last-of-type::after {
  content: "";
  margin: 0;
  background-image: none;
}

.c-breadcrumb__item--ja {
  font-family: "Noto Sans JP", sans-serif;
}
@media screen and (min-width: 900px) {
  .c-breadcrumb__item--ja {
    font-size: 1.25rem;
  }
}

.c-breadcrumb--white .c-breadcrumb__item:last-of-type {
  color: #FFFFFF;
}

.c-breadcrumb--white .c-breadcrumb__item::after {
  background-color: #8A8A8A;
}

.c-breadcrumb--white .c-breadcrumb__item:nth-last-of-type(2)::after {
  background-color: #FFFFFF;
}

.c-btn1 {
  position: relative;
  display: inline-block;
  max-width: 17.5rem;
  width: 100%;
  padding: 0.8125rem 0;
  background-color: #FBD700;
  text-align: center;
  transition: all 0.3s;
  border-radius: 25px;
}

.c-btn1__text {
  font-size: 1.0625rem;
  padding-bottom: 0.1875rem;
  font-weight: 700;
  background-image: linear-gradient(#000, #000);
  background-repeat: no-repeat;
  background-position: bottom right; /* 下線の初期位置 */
  background-size: 0 1px; /* 下線のサイズ（横幅、高さ） */
  transition: background-size 0.3s;
}

.c-btn1::after {
  content: "";
  position: absolute;
  top: 50%;
  right: 1.0625rem;
  transform: translateY(-50%);
  mask-image: url(../../assets/images/common/arrow.svg);
  background-color: #000000;
  mask-repeat: no-repeat;
  mask-position: center;
  mask-size: contain;
  width: 2.5rem;
  height: 0.6875rem;
  transition: all 0.3s;
}

.c-btn1:hover .c-btn1__text {
  background-position: bottom left; /* 下線のホバー時位置 */
  background-size: 100% 1px; /* 下線の横幅を100%にする */
}

.c-btn1:hover::after {
  right: 0.3125rem;
}

/* ---------- ボタン共通設定 | ここから ---------- */
.c-btn2 {
  display: inline-flex;
  justify-content: center;
  align-items: center;
  max-width: 13.5rem;
  width: 100%;
  height: 4.25rem;
  gap: 1.3125rem;
  background-color: #FBD700;
  transition: all 0.3s;
  border-radius: 35px;
  padding-left: 20px;
}

.c-btn2__text {
  font-size: 1.125rem;
  font-weight: 700;
  color: #000000;
  transition: all 0.3s;
  color: transparent; /* テキスト本体は透明にする */
  /*
   影の設定
   1つ目：Y方向の位置にマイナスに設定してテキスト本体の上に配置、ぼかしなし
   2つ目：位置はテキスト本体と同じ、ぼかしなし
  */
  text-shadow: 0 -1.5em 0 #000, 0 0 0 #000;
  transition: text-shadow 0.3s;
  position: relative;
  overflow: hidden; /* 1つ目の影を隠す */
}

.c-btn2__text::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  text-shadow: 0 -3em 0 #000, 0 0 0 #000;
  transition: text-shadow 0.3s;
  overflow: hidden; /* 1つ目の影を隠す */
  width: 100%;
  height: 100%;
}

.c-btn2:hover .c-btn2__text {
  text-shadow: 0 0 0 #000, 0 1.5em 0 #000; /* 2つの影の位置を上方向に1.5emずつずらす */
}

.c-btn2__iconbg {
  background-color: #000000;
  border-radius: 50%;
  width: 2.25rem;
  height: 2.25rem;
  position: relative;
  overflow: hidden;
  transition: all 0.3s;
  margin-left: 15px;
}

.c-btn2__iconbg::before {
  content: "";
  position: absolute;
  background-color: #FFFFFF;
  top: 0;
  left: -130%;
  transition: left 200ms ease-out 0s;
  transform: skewX(-25deg);
  width: 120%;
  height: 120%;
}

.c-btn2__icon {
  position: absolute;
  inset: 0;
  z-index: 10;
  width: 100%;
  height: 100%;
  border-radius: 50%;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

.c-btn2__icon::before,
.c-btn2__icon::after {
  opacity: 1;
  transition: translate 350ms ease-out 150ms;
  content: "";
  display: block;
  position: absolute;
  width: 0.75rem;
  height: 0.5rem;
  mask-image: url(../../assets/images/common/arrow.svg);
  mask-position: center;
  mask-repeat: no-repeat;
  mask-size: contain;
}

.c-btn2__icon::before {
  transform: translateX(0);
  background-color: #FFFFFF;
}

.c-btn2__icon::after {
  transform: translateX(-100%);
  transition: all 0.5s;
  background-color: #000000;
}

.c-btn2:hover .c-btn2__iconbg {
  background-color: #FFFFFF;
}

.c-btn2:hover .c-btn2__iconbg::before {
  left: -15%;
}

.c-btn2:hover .c-btn2__icon::after {
  transform: translateX(0);
  transition: all 0.5s;
}

