.c-send-btn {
  width: 15.375rem;
  height: 3.5rem;
  transition: all 0.3s;
  position: relative;
  display: inline-block;
}

.c-send-btn a {
  display: block;
}

.c-send-btn__phone {
  position: absolute;
  top: 50%;
  left: 0.5rem;
  transform: translateY(-50%);
  font-size: 1.0625rem;
  font-family: "Inter", sans-serif;
  font-weight: 500;
  line-height: 0.7058823529;
  letter-spacing: 0.03em;
  color: #000000;
  background-color: #FFFFFF;
  padding: 0.875rem 0.75rem 0.875rem 2.625rem;
  border-radius: 35px;
  z-index: 2;
}

.c-send-btn__telicon {
  position: relative;
}

.c-send-btn__telicon::before {
  position: absolute;
  content: "";
  top: 50%;
  left: -1.875rem;
  transform: translateY(-50%);
  mask-image: url(../../assets/images/common/phone-icon.svg);
  mask-repeat: no-repeat;
  mask-position: center;
  mask-size: contain;
  background-color: #000000;
  width: 1.375rem;
  height: 1.3125rem;
}

.c-send-btn__phone:hover .c-send-btn__telicon::before {
  animation: hoverPulsate 1s infinite;
}

@keyframes hoverPulsate {
  0% {
    transform: translate(0px, -50%);
  }
  7% {
    transform: translate(-3px, -50%);
  }
  14% {
    transform: translate(3px, -50%);
  }
  20% {
    transform: translate(-3px, -50%);
  }
  32% {
    transform: translate(3px, -50%);
  }
  40% {
    transform: translate(0px, -50%);
  }
}
