}

/* ローディングアニメーション */
.p-loading-animation {
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #000000; /* 初期状態は赤背景 */
  width: 100vw;
  height: 100vh;
  position: fixed;
  transition: all 1s;
  z-index: 9999;
  opacity: 0; /* 背景をフェードアウト */
  visibility: hidden; /* 完全に非表示にする */
}

/* ローディングアニメーション中 */
.p-loading-animation.is-active {
  transition: all -1s;
  opacity: 1;
  visibility: visible;
}

