    max-width: 37.5rem; /*SVGタグの横幅*/
  }
}

/* マスクをするパスの設定*/
#mask .cls-1 {
  fill: none;
  stroke: #FFFFFF;
  stroke-width: 5.5; /*線の太さを指定する*/
  stroke-miterlimit: 10;
}

/* 背景色のフェードインアニメーション */
@keyframes fadeInBackground {
  0% {
    fill: transparent;
  }
  100% {
    fill: #FFFFFF; /* お好みの背景色 */
  }
}
.svg-container svg #loading .cls-1 {
  animation: fadeInBackground 2s ease forwards;
}

