.wp-pagenavi a:first-child {
  margin-left: 0;
}

.wp-pagenavi a:nth-of-type(3),
.wp-pagenavi a:nth-of-type(4) {
  display: none;
}
@media screen and (min-width: 900px) {
  .wp-pagenavi a:nth-of-type(3),
  .wp-pagenavi a:nth-of-type(4) {
    display: inline-flex;
  }
}

.wp-pagenavi a:hover {
  color: #FFFFFF;
  background-color: #212121;
  opacity: 1;
}

.wp-pagenavi .current {
  color: #FFFFFF;
  background-color: #212121;
}